import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import ListIcon from '@material-ui/icons/List';
import AccountIcon from '@material-ui/icons/AccountCircleOutlined';

import { MY_PROFILE_QUERY } from './../graphql/queries'
import List from '@material-ui/core/List'; 
import './UserProfile.css';
import { ListItem } from '@material-ui/core';
import { Query } from 'react-apollo';

import UserCollection from '../components/UserCollection';
import { COLLECTIONSTATE } from '../globalEnum';


const styles = {
    avatar: {
        width: "240px",
        height: "240px"
    },
    icon: {
        fontSize: "20px"
    },
    listItem: {
        fontSize: "18px",
        fontFamily: "Quicksand",
        fontWeight: "100"
    },
    listItemText: {
        width: "50%",
    },
    listItemNum: {
        marginRight: "0",
        marginLeft: "auto",
        color: "rgb(183, 183, 183)"
    },
    rightAlign: {
        marginRight: "0",
        marginLeft: "auto"
    }
}

class UserProfile extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            currCollectionId: undefined,
            following: true
        }
    }
    

    chooseCollection = id => {
        this.setState({
            currCollectionId: id,
        })
    }

    toggleFollow = () => {
        this.setState(prevState => ({
            following: !prevState.following
        }))
    }
    render(){
        const { match: { params }, currentUserId } = this.props;
        if(!currentUserId) currentUserId = this.props.location.currentUserId;
        return(
            <Query query={MY_PROFILE_QUERY} variables={{userID: params.id.toString()}} >
                { ({ loading, error, data, subscribeToMore, refetch}) => {
                    if (loading) return <p>Loading...</p>
                    if (error) return <p>Error :(((</p>
                    var { name, avatar, collections } = data.user
                    const collection = collections.find(collection => collection.id === this.state.currCollectionId);
                    const publishedCollections = collections.filter(collection => collection.state === COLLECTIONSTATE.PUBLISHED);
                    //console.log(publishedCollection);
                    return(
                    <div className="UserProfile animated fadeIn slow">
                        <div className="UserProfile-flex-container">
                            <div className="UserProfile-margin-block" ></div>
                            <div className="UserProfile-left" >
                                <div className="inner-container">
                                    {
                                        avatar?
                                        <Avatar src={avatar} style={styles.avatar}/>:
                                        <Avatar style={styles.avatar}><AccountIcon style={styles.avatar}/></Avatar>
                                    }
                                    <div className="follows-container">
                                        <div className="top-block">{name}</div>
                                        <div className="block">
                                            <div><ListIcon style={styles.icon} /></div>
                                            <div style={styles.rightAlign}>{publishedCollections.length} Collections</div>
                                        </div>
                                    </div>
                                </div>
                                <div className="public-card-container">
                                    {collection?<UserCollection collection={collection} userID={currentUserId.toString()}/>:<div></div>}
                                </div>
                            </div>
                            <div className="UserProfile-right" >
                                <div className="top-block">
                                    Collections
                                </div>
                                <List style={{maxHeight: "100%", width: "80%", overflow: "auto"}}>
                                    {
                                        publishedCollections.map((item, idx) => 
                                            <ListItem 
                                                button 
                                                key={idx}
                                                style={styles.listItem}
                                                onClick={() => this.chooseCollection(item.id)}
                                            >
                                                <div style={styles.listItemText}>{item.title}</div>
                                                <div style={styles.listItemNum}>{item.bookmarks.length}</div>
                                            </ListItem>)
                                    }
                                </List>
                            </div>
                            <div className="UserProfile-margin-block" ></div>
                        </div>
                    </div>)
                }}
            </Query>
        )
    }
}

export default UserProfile;