import React from 'react';
import { Avatar, Typography } from '@material-ui/core';
import AccountIcon from '@material-ui/icons/AccountCircleOutlined';

import { Card, CardContent } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import GotoIcon from '@material-ui/icons/NearMeOutlined';
import IconButton from '@material-ui/core/IconButton';
import { NavLink } from 'react-router-dom';
import { USER_COLLECTION_QUERY } from '../graphql/queries';
import { Query } from 'react-apollo';
import { COLLECTIONSTATE } from '../globalEnum';

const useStyles = makeStyles({
    card: {
        width: "100%",
        marginBottom: "10px",
        height: "120px"
    },
    avatar:{
        marginTop: "1rem",
        marginBottom: "1rem",
        marginLeft: "1rem",
        marginRight: "1rem",
        height: '4rem',
        width: "4rem",
    },
    icon: {
        fontSize: "100px",
    },
    iconButton:{
        width: "60px",
        height: "60px",
        marginTop: "30px",
        marginBottom: "30px",
        border: "1px solid rgb(224, 224, 224)",
    },
    link: {
        color: "rgb(173, 173, 173)"
    }
});

export default function UserCard(props){
    const classes = useStyles();
    const { currentUserId, user } = props
    //console.log(user.avatar);

    const avatar = user.avatar ? 
        <Avatar className={classes.avatar} src={user.avatar} />:
        <Avatar className={classes.avatar}><AccountIcon className={classes.icon}/></Avatar>;

    
    return(
        <Query query={USER_COLLECTION_QUERY} variables={{userID: user.id}}>
            {({data}) => {
                let length = 0;
                if(data.user) length = data.user.collections.filter(collection => collection.state === COLLECTIONSTATE.PUBLISHED).length;
                return(
                    <Card className={classes.card}>
                        <CardContent>
                            <div className="flex-container">
                                <div >{avatar}</div>
                                <div className="user-info">
                                    <div className="user-name"> {user.name} </div>
                                    <div className="user-collections">{length} collections</div>
                                </div>
                                <div className="button-container animated pulse infinite">
                                    <IconButton 
                                        className={classes.iconButton}
                                    >
                                        {
                                            currentUserId === user.id ?
                                            <NavLink 
                                                to={`/myprofile`} 
                                                className={classes.link}
                                            >
                                                    <GotoIcon/>
                                            </NavLink>:
                                            <NavLink 
                                                to={`/user/${user.id}`} 
                                                className={classes.link}
                                            >
                                                <GotoIcon/>
                                            </NavLink>
                                        }
                                        
                                    </IconButton>
                                </div>
                            </div>
                        </CardContent>
                        
                    </Card>
                )
            }}
        </Query>
        
    )
}
