import React from 'react';
import TextWrapper from '../../components/TextWrapper';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import AccountIcon from '@material-ui/icons/AccountCircleOutlined';
import "./Login.css";
import { borders } from '@material-ui/system'
import { NavLink, Redirect } from 'react-router-dom';
import { CREATE_USER_MUTATION } from '../../graphql/mutations';
import { Mutation } from 'react-apollo';
import uuidv4 from 'uuid/v4';
const validator = require('validator');

const styles = {
    button: {
        width: "100%",
        fontFamily: "Quicksand",
        fontWeight: "100",
        fontSize: "15px",
    },
    avatar: {
        width: "200px",
        height: "200px",
    }
}

class Register extends React.Component {
    state = {
        username: "",
        password: "",
        email: "",
        emptyUsername: false,
        emptyPassword: false,
        emptyEmail: false,
        invalidEmail: false,
        imageUrl: "",
        dataUrl: "",
        successfulRegister: false,
    }

    createUserFunc

    onUserInput = e => {
        this.setState({
            username: e.target.value,
        })
        if(e.target.value.length > 0){
            this.setState({
                emptyUsername: false
            })
        }
    }

    onPasswordInput = e => {
        this.setState({
            password: e.target.value,
        })
        if(e.target.value.length > 0){
            this.setState({
                emptyPassword: false
            })
        }
    }

    onEmailInput = e => {
        this.setState({
            email: e.target.value,
        })
        if(e.target.value.length > 0){
            this.setState({
                emptyEmail: false
            })
        }
    }

    handleRegister = () => {
        console.log("handling register");
        if(this.validateForm()){
            console.log("register!");
            this.setState({
                successfulRegister: true,
            })

            this.createUserFunc({
                variables: {
                    id: uuidv4(),
                    name: this.state.username,
                    password: this.state.password,
                    email: this.state.email,
                    avatar: this.state.dataUrl,
                }
            })
            
            console.log(this.state);
        }
    }

    validateForm = () => {
        if(this.state.username.length === 0)
            this.setState({
                emptyUsername: true,
            })
        if(this.state.password.length === 0)
            this.setState({
                emptyPassword: true,
            })
        if(this.state.email.length === 0)
            this.setState({
                emptyEmail: true,
            })
        else{
            this.setState({
                invalidEmail: !validator.isEmail(this.state.email),
            })
        }
        return this.state.username.length > 0 && 
                this.state.password.length > 0 && 
                    this.state.email.length > 0 && 
                        validator.isEmail(this.state.email)
    }

    onUpload = event => {
		const file = event.target.files[0];
		this.setState({
			imageUrl: URL.createObjectURL(file)
		})
		const reader = new FileReader();
		reader.addEventListener("load", () => {
			this.setState({
				dataUrl: reader.result,
            });
		})
		reader.readAsDataURL(file);
	}

    render(){
        return(
            <Mutation mutation={CREATE_USER_MUTATION}>
                {(createUser, {data}) => {
                    this.createUserFunc = createUser;
                    let duplicateEmail = false;
                    if(data) duplicateEmail = (data.createUser.status === "DUPLICATE_EMAIL_ERROR");
                    if(this.state.successfulRegister && data && !duplicateEmail) return <Redirect to='/'/>;
                    else{
                        return(
                            <div className="register-box">
                                <div className="animated fadeIn delay-2s" style={{width: "200px", margin: "0 auto", marginBottom: "5vh"}}>
                                    <input
                                        accept="image/*"
                                        style={{ display: 'none' }}
                                        id="upload"
                                        type="file"
                                        onChange={this.onUpload}
                                    />
                                    <label htmlFor="upload" className="image-upload">
                                        {
                                            (this.state.imageUrl === "") ? 
                                            (<Avatar style={styles.avatar}><AccountIcon style={{fontSize:"200px"}}/></Avatar>):
                                            (<Avatar style={styles.avatar} src={this.state.dataUrl} />)
                                        }  
                                    </label>
                                </div>
                                <div className="input-container animated slideInDown">
                                    <TextWrapper 
                                        label="Username"
                                        value={this.state.username}
                                        handleChange={this.onUserInput}
                                    />
                                </div>
                                <div>{(this.state.emptyUsername) ? (<div className="warning">Username should not be empty!</div>) : (<div></div>)}</div>
                                <div className="input-container animated slideInDown">
                                    <TextWrapper 
                                        label="Password"
                                        value={this.state.password}
                                        handleChange={this.onPasswordInput}
                                    />
                                </div>
                                <div>{(this.state.emptyPassword) ? (<div className="warning">Password should not be empty!</div>) : (<div></div>)}</div>
                                <div className="input-container animated slideInDown">
                                    <TextWrapper 
                                        label="Email"
                                        value={this.state.email}
                                        handleChange={this.onEmailInput}
                                    />
                                </div>
                                <div>
                                    {
                                        this.state.emptyEmail ? 
                                            (<div className="warning">Email should not be empty!</div>) : 
                                            (
                                                this.state.invalidEmail?
                                                    (<div className="warning">Invalid email format!</div>):
                                                    (duplicateEmail ?
                                                        <div className="warning">Email has already been taken!</div>:
                                                        <div></div>
                                                    )
                                            )
                                    }
                                </div>
                                <div className="animated slideInUp">
                                    <div className="button-container animated pulse infinite">
                                        <Button onClick={() => this.handleRegister()} style={styles.button}>
                                            Register
                                        </Button>          
                                    </div>
                                </div>
                            </div>
                        )
                    }
                }}
            </Mutation>
        )
    }
}

export default Register;