import SearchIcon from '@material-ui/icons/Search';

import BookmarksTable from './BookmarksTable';

import { Query, Mutation } from 'react-apollo';
import { ADD_COLLECTION_MUTATION, UPDATE_COLLECTION_STATE_MUTATION } from '../graphql/mutations';
import { COLLECTIONSTATE } from '../globalEnum';



const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: 'left',
      display: 'column',
      justifyContent: 'center',
      color: theme.palette.text.secondary,
      width: '90%',
      margin: "0 auto"
    },
    textField: {
        borderColor: "#4E5167",
        color: "#4E5167",
        outlineColor:  "#4E5167",
        '&:hover': {
            color: "#4E5167",
        },
    },
    typography :{
        fontSize: "1.5em",
        color: "#4E5167",
        fontSize: '2em',
        fontFamily: 'Quicksand'
    },
    typoStar: {
        paddingRight: '10px'
    },
    chip: {
        margin: theme.spacing(1),
        backgroundColor: blueGrey[100],
    },
    button: {
        borderColor: blueGrey[100],
        color: "#4E5167",
        borderColor: "#4E5167",
    },
    div: {
        display: 'flex', 
        justifyContent: 'left',
        marginRight: 'auto'
    },
    div1: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        //paddingBottom: '10px'
        width: "100%"
    },
    div2: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
    },
    div3: {
        display: 'flex', 
        justifyContent: 'left',
        marginRight: 'auto'
    },
    div4: {
        display: 'flex',
        direction: 'row',
        alignItems: 'center'
    },
    div5 : {
        width: '100%',
        height: '15px',
    },
    title: {
        fontFamily: "Quicksand",
        fontSize: "2em",
        color: "#4E5167",
    },
    descriptionInput: {
        width: "50%"
    }
  }));

export default function UserCollection (props) {
    const classes = useStyles();
    const {id, title, description, bookmarks, tags, publishedTime, state} = props.collection
    const { whoAmI, userID } = props

    console.log("inside", props.collection);
    const [searchVal, setSearchVal] = React.useState("");

    function handleChange(e){
        setSearchVal(e.target.value);
    }
    return (
      <div style={{width:"100%"}}className={classes.root}>
                <Grid container spacing={2} direction="row" justify="flex-start" alignItems="flex-start">
                    <Grid container xs={12} sm={12}> 
                        <Paper className={classes.paper}>
                            <Grid item xs={12}>     
                                <div className={classes.div1}>
                                    <div className={classes.div}>
                                        <div className={classes.title}>{title}</div>
                                    </div>
                                    <TextField
                                        id="outlined-search"
                                        placeholder='Search in Collection'
                                        type="search"
                                        className={classes.textField}
                                        margin="normal"
                                        variant="outlined"
                                        InputProps={{
                                            startAdornment: <IconButton> <SearchIcon /> </IconButton>
                                        }}
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                        onChange={handleChange}
                                        value={searchVal}
                                    />
                                </div>
                                <Typography variant='body2' color='textSecondary' paragraph>
                                    Tags
                                    {tags.map(tag => 
                                        <Chip
                                        size="small"
                                        label={tag}
                                        clickable
                                        className={classes.chip}
                                    />)}
                                    <Divider variant='middle'/>
                                </Typography>

                                <Typography variant='body2' color='textSecondary' > Published Date </Typography>
                                <Typography variant='body1' color='textPrimary' paragraph>
                                    <div className={classes.div3}>
                                        <div className={classes.div3}> {publishedTime} </div>
                                            {
                                                whoAmI === 'Me'?
                                               <Mutation mutation={UPDATE_COLLECTION_STATE_MUTATION} >
                                                {   updateCollection => {
                                                    return( 
                                                        <Tooltip title={`Add \'${title}\' to your private collection`}>
                                                        <Button variant='outlined' size='medium' 
                                                                className={classes.button} 
                                                                onClick={event => {
                                                                    updateCollection({
                                                                        variables:{
                                                                            collectionId: id,
                                                                            state: COLLECTIONSTATE.PRIVATE
                                                                        }
                                                                    });
                                                                    props.onClickStore();
                                                                }    
                                                            } 
                                                        >
                                                            Store Up
                                                        </Button>
                                                        </Tooltip>)  
                                                    }
                                                }
                                                </Mutation>:
                                                <Mutation mutation={ADD_COLLECTION_MUTATION} >
                                                {   addCollection => {
                                                    console.log(bookmarks);
                                                    return( 
                                                        <Tooltip title={`Add \'${title}\' to your favorite collection`}>
                                                        <Button variant='outlined' size='medium' className={classes.button} onClick={event => {
                                                            addCollection({
                                                                variables:{
                                                                    userID: userID,
                                                                    collectionId: id,
                                                                    collections: {
                                                                        title: title,
                                                                        description: description,
                                                                        tags: tags,
                                                                        bookmarks: bookmarks.map(bookmark => {
                                                                            delete bookmark["__typename"];
                                                                            return bookmark;
                                                                        })
                                                                    },
                                                                }
                                                            })
                                                            //props.onClickFavorite();    
                                                        }
                                                        } >
                                                            Favorite
                                                        </Button>
                                                        </Tooltip>)  
                                                    }
                                                }
                                                </Mutation>
                                            }
                                           
                                        
                                        <div style={{marginLeft: "2%"}}></div>
                                    </div>
                                </Typography>

                                <Typography variant='body2' color='textSecondary'> Description </Typography>
                                <Typography variant='body1' color='textPrimary' paragraph>{description}</Typography> 
                                <div className={classes.div2}>
                                    <div className={classes.div3}> <Typography variant='body2' color='textSecondary'> Bookmarks </Typography> </div>               
                                </div>
                                <BookmarksTable 
                                    filterStr={searchVal} 
                                    bookmarks={bookmarks}
                                    isSelf={false}>
                                </BookmarksTable>
                            </Grid>
                        </Paper>  
                    </Grid>
                </Grid>
        </div>
    );
  }