import uuidv4 from 'uuid/v4';
import { COLLECTIONSTATE } from '../globalEnum';
import { getUrlMetaData } from './scrapeUtils';
const $ = require("cheerio");

function parseBookmarkHtml(html){
    var bookmarks = {};
    $("a", html).each((index, a) => {
        var $a = $(a);
        var title = $a.text();
        var url = $a.attr("href");
        var folder = getFolder($a);
        if(!bookmarks[folder]) bookmarks[folder] = []
        bookmarks[folder].push([title, url]);
        console.log('url: ', url)
    });
    var collections = [];
    Object.keys(bookmarks).forEach(folder => {
        var collection = {};
        collection.id = uuidv4();
        collection.author = {name: "Brian", avatar: "hello.com" };
        collection.title = folder;
        collection.description = "";
        collection.tags = [];
        collection.state = COLLECTIONSTATE.PRIVATE;
        collection.publishedTime = null;
        collection.bookmarks = bookmarks[folder].map(([title, url]) => {
            return {
                id: uuidv4(),
                title: title,
                url: url,
                icon: "",
                shortUrl: "",
                description: ""
            }
        })
        collections.push(collection);
    })
    return collections;
}

function generateBookmarkHtml(collections){
    var content = '<!DOCTYPE NETSCAPE-Bookmark-file-1><HTML><META HTTP-EQUIV="Content-Type" CONTENT="text/html; charset=UTF-8"><Title>Bookmarks</Title><H1>Bookmarks</H1>';
    collections.forEach(collection => {
        content += (`<DT><H3 FOLDED>${collection.title}</H3><DL><p>`);
        collection.bookmarks.forEach(bookmark => {
            content += (`<DT><A HREF="${bookmark.url}">${bookmark.title}</A>`);
        })
        content += '</DL><p>';
    })
    content += '</HTML>';
    return content;
}

