function getFolder($a) {
    var $node = $a.closest("DL").prev();
    var title = $node.text()
    return ($node.length > 0 && title.length > 0) ? [title].concat(getFolder($node)) : [];
}

export { parseBookmarkHtml, generateBookmarkHtml };