import React, { Component } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { blueGrey } from '@material-ui/core/colors';
import Button from '@material-ui/core/Button';
import Chip from '@material-ui/core/Chip';
import Divider from '@material-ui/core/Divider';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import Paper from '@material-ui/core/Paper';
import Tooltip from '@material-ui/core/Tooltip'
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';

import SearchIcon from '@material-ui/icons/Search';
import FavoriteIcon from '@material-ui/icons/Favorite';

import BookmarksTable from './BookmarksTable';
import BookmarksAdd from './BookmarksAdd';
import CommentCard from './CommentCard';

import { COLLECTIONSTATE } from '../globalEnum';
import { Query, Mutation } from 'react-apollo';
import { UPDATE_COLLECTION_MUTATION, DELETE_COLLECTION_MUTATION } from '../graphql/mutations';



const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: 'left',
      display: 'column',
      justifyContent: 'center',
      color: theme.palette.text.secondary,
      width: '90%',
      margin: "0 auto"
    },
    textField: {
        borderColor: "#4E5167",
        color: "#4E5167",
        outlineColor:  "#4E5167",
        '&:hover': {
            color: "#4E5167",
        },
    },
    typography :{
        fontSize: "1.5em",
        color: "#4E5167",
        fontSize: '2em',
        fontFamily: 'Quicksand'
    },
    typoStar: {
        paddingRight: '10px'
    },
    chip: {
        margin: theme.spacing(1),
        backgroundColor: blueGrey[100],
    },
    button: {
        color: "#4E5167",
        borderColor: "#4E5167",
    },
    deletedButton:{
        backgroundColor: "red",
        color: "white",
    },
    div: {
        display: 'flex', 
        justifyContent: 'left',
        marginRight: 'auto'
    },
    div1: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        //paddingBottom: '10px'
        width: "100%"
    },
    div2: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
    },
    div3: {
        display: 'flex', 
        justifyContent: 'left',
        marginRight: 'auto'
    },
    div4: {
        display: 'flex',
        direction: 'row',
        alignItems: 'center'
    },
    div5 : {
        width: '100%',
        height: '15px',
    },
    titleInput: {
        fontFamily: "Quicksand",
        fontSize: "2em",
        color: "#4E5167",
    },
    descriptionInput: {
        width: "50%"
    }
  }));

export default function CollectionCard(props) {
    const classes = useStyles();
    const {id, title, description, bookmarks, tags, publishedTime, state} = props.collection
    
    const [searchVal, setSearchVal] = React.useState("");
    const [titleVal, setTitleVal] = React.useState(title);
    const [descriptionVal, setDescrptionVal] = React.useState(description);
    const [stateVal, setStateVal] = React.useState(state);
    const [publishTimeVal, setPublishTimeVal] = React.useState(publishedTime);
    const [deleteBool, setDeleteBool] = React.useState(false);
    const [onChange, setChange] = React.useState(false);


    React.useEffect(() => {
        setStateVal(props.collection.state);
        setTitleVal(props.collection.title);
        setDescrptionVal(props.collection.description);
        setPublishTimeVal(props.collection.publishedTime);
    }, [props.collection.state, 
        props.collection.title, 
        props.collection.description,
        props.collection.publishedTime
    ]);

    var updateCollectionFunc;
    var deleteCollectionFunc

    const handleChange = e => {
        setSearchVal(e.target.value);
        setChange(true);
    }

    const handleTitleChange = e => {
        setTitleVal(e.target.value);
        setChange(true);
    }

    const handleDescriptionChange = e => {
        setDescrptionVal(e.target.value);
        setChange(true);
    }

    const handleDelete = e => {
        setDeleteBool(true);
        setChange(true);
    }

    const toggleState = () => {
        if(stateVal === COLLECTIONSTATE.PUBLISHED){
            setPublishTimeVal(null);
            setStateVal(COLLECTIONSTATE.PRIVATE);
        }
        if(stateVal === COLLECTIONSTATE.PRIVATE){
            const now = new Date()
            setPublishTimeVal(now.toLocaleString());
            setStateVal(COLLECTIONSTATE.PUBLISHED);
        }
        setChange(true);
    }

    const handleCollectionUpdate = e => {
        //e.preventDefault();
        //if(!titleVal || !descriptionVal) return;

        console.log(id, stateVal, titleVal, descriptionVal, publishTimeVal);
        //console.log(id, stateVal, titleVal, descriptionVal, deleteBool);
        console.log(deleteBool)
        updateCollectionFunc({
            variables: {
                collectionId: id.toString(),
                title: titleVal,
                description: descriptionVal,
                state: stateVal,
                publishedTime: publishTimeVal
            }
        })
        if(deleteBool)
            deleteCollectionFunc({
                variables: {
                    collectionId: id.toString(),
                }
            })
        props.onSaveUpdate();
        setChange(false);
    }

    return (
      <div style={{width:"100%"}}className={classes.root}>
          <Mutation mutation={UPDATE_COLLECTION_MUTATION}>
              {updateCollection => {
                  updateCollectionFunc = updateCollection;
                    return(
                        <Mutation mutation={DELETE_COLLECTION_MUTATION}>
                            {
                                deleteCollection => {
                                    deleteCollectionFunc = deleteCollection
                                    return(
                                        <Grid container spacing={2} direction="row" justify="flex-start" alignItems="flex-start">
                                        <Grid container xs={12} sm={12}> 
                                            <Paper className={classes.paper}>
                                                <Grid item xs={12}>     
                                                    <div className={classes.div1}>
                                                        <div className={classes.div}>
                                                            <TextField 
                                                                value={titleVal}
                                                                InputProps={{
                                                                    classes: {
                                                                    input: classes.titleInput
                                                                    },
                                                                }}
                                                                onChange={handleTitleChange}
                                                            />
                                                            </div>
                                                        {/*<Tooltip title='Number of likes'>
                                                            <FavoriteIcon />
                                                        </Tooltip>
                                                        <Typography variant='h6' className={classes.typoStar} > {'X' + likes} </Typography>
                                                        */}
                                                        <TextField
                                                            id="outlined-search"
                                                            placeholder='Search in Collection'
                                                            type="search"
                                                            className={classes.textField}
                                                            margin="normal"
                                                            variant="outlined"
                                                            InputProps={{
                                                                startAdornment: <IconButton> <SearchIcon /> </IconButton>
                                                            }}
                                                            InputLabelProps={{
                                                                shrink: true,
                                                            }}
                                                            onChange={handleChange}
                                                            value={searchVal}
                                                        />
                                                    </div>
                                                    <Typography variant='body2' color='textSecondary' paragraph>
                                                        Tags
                                                        {tags.map(tag => 
                                                            <Chip
                                                            size="small"
                                                            label={tag}
                                                            clickable
                                                            className={classes.chip}
                                                        />)}
                                                        <Divider variant='middle'/>
                                                    </Typography>
                                                    <Typography variant='body2' color='textSecondary' > Published Date </Typography>
                                                    <Typography variant='body1' color='textPrimary' paragraph>
                                                        <div className={classes.div3}>
                                                            <div className={classes.div3}> {publishedTime} </div>
                                                            <Tooltip title={`Make \'${title}\' go ${(stateVal === COLLECTIONSTATE.PRIVATE ? "public" : "private")}`}>
                                                                <Button variant='outlined' size='medium' className={classes.button} onClick={toggleState}>
                                                                    {(stateVal === COLLECTIONSTATE.PRIVATE)? "Publish" : "Archive"}
                                                                </Button>
                                                            </Tooltip>
                                                            <div style={{marginLeft: "2%"}}></div>
                                                            <Tooltip title={`Delete collection  \'${title}\'`}>
                                                                <Button 
                                                                    variant='outlined' 
                                                                    size="medium" 
                                                                    className={deleteBool?classes.deletedButton+" animated flash infinite":classes.button}
                                                                    onClick={handleDelete}
                                                                >
                                                                    {deleteBool?"Deleted!":"Delete"}
                                                                </Button>
                                                            </Tooltip>
                                                            <div style={{marginLeft: "2%"}}></div>
                                                            <Tooltip title="Save changes to collection">    
                                                                <div className={onChange?"animated infinite flash slow":""}>
                                                                    <Button 
                                                                        variant='outlined' 
                                                                        size="medium" 
                                                                        className={classes.button}
                                                                        onClick={handleCollectionUpdate}
                                                                    >
                                                                        Save
                                                                    </Button>
                                                                </div>
                                                            </Tooltip>
                                                        </div>
                                                    </Typography>
                                                    <Typography variant='body2' color='textSecondary'> Description </Typography>
                                                    <TextField 
                                                        value={descriptionVal}
                                                        onChange={handleDescriptionChange}
                                                        className={classes.descriptionInput}
                                                        multiline={true}
                                                    />
                                                    <div className={classes.div2}>
                                                        <div className={classes.div3}> <Typography variant='body2' color='textSecondary'> Bookmarks </Typography> </div>               
                                                        <BookmarksAdd
                                                            collectionId={id} 
                                                            onAddBookmark={props.onAddBookmark} 
                                                            collectionTitle={title}
                                                        />
                                                    </div>
                                                    <BookmarksTable 
                                                        filterStr={searchVal} 
                                                        bookmarks={bookmarks} 
                                                        onDeleteBookmark={props.onDeleteBookmark}
                                                        isSelf={true}>
                                                    </BookmarksTable>
                                                </Grid>
                                            </Paper>  
                                        </Grid>
                                        {/*<Grid item xs={12} sm={4}>
                                            <Paper className={classes.paper}>
                                                <Typography variant='h4' className={classes.typography}> Comments </Typography>
                                                <Typography variant='body2' color='textSecondary' paragraph > From others to improve your collections. </Typography>
                                                <Divider variant='middle'></Divider>
                                                <Grid item xs={12} spacing={2}>
                                                {   
                                                    comments.map(comment => <>
                                                        <div className={classes.div5}> </div>
                                                        <CommentCard comment={comment} /> </>)
                                                }
                                                </Grid>
                                            </Paper>
                                            </Grid>*/}
                                        </Grid>
                                      )
                                }
                            }
                        </Mutation>
                    )
              }}
        </Mutation>
      </div>
    );
  }