export const FEEDS_QUERY = gql`
query {
    collections{
        id
        title
        description
        likes
        tags
        bookmarks {
            id
            title
            description
            url
            icon
            shortUrl
        }
        publishedTime
        author {
            id
            name
            avatar
        }
    }
}`

export const USER_COLLECTION_QUERY = gql`
    query user($userID: String!){
        user(
            userID: $userID
        ){
            collections {
                state
            }
        }
    }
`

export const MY_PROFILE_QUERY = gql`
query user($userID: String!){
    user(
        userID: $userID
    ){
        name
        avatar
        followers {
            name
            avatar
        }
        following {
            name
            avatar
        }
        collections {
            id
            title
            description
            likes
            tags
            bookmarks {
                id
                title
                description
                url
                icon
                shortUrl
            }
            state
            publishedTime      
        }
    }
}
`

export const ADD_COLLECTION_QUERY = gql`
    query collections(
        $collectionId: String
    ){
        collections(
            collectionId: $collectionId
        ){
            title
            description
            tags 
        }
    }
`

export const SEARCH_COLLECTIONS_QUERY = gql`
    query collections(
        $title: String
        $collectionId: String
    ){
        collections(
            title: $title
            collectionId: $collectionId
        ){
            id
            title
            description
            likes
            tags
            bookmarks {
                id
                title
                description
                url
                icon
                shortUrl
            }
            publishedTime
            author {
                name
                avatar
            }
        }
    }
`

export const SEARCH_USERS_QUERY = gql`
    query users(
            $name: String!
        ){
            users(
                name: $name
            ){
                id
                name
                avatar
            }
        }
`

export const SEARCH_USERS_COLLECTIONS_QUERY = gql`
    query userCollections(
        $userID: String!
    ){
        userCollections(
            userID: $userID
        ){
            id
        }
    }
`