import React from 'react';
import { Query, Mutation } from 'react-apollo'
import { MY_PROFILE_QUERY, ADD_COLLECTION_QUERY } from './../graphql/queries'
import { IMPORT_COLLECTIONS_MUTATION, ADD_COLLECTION_MUTATION } from './../graphql/mutations'
import { ADD_BOOKMARK_SUBSCRIPTION, DELETE_BOOKMARK_SUBSCRIPTION} from '../graphql/subscriptions';
import Avatar from '@material-ui/core/Avatar';
import ListIcon from '@material-ui/icons/List';
import FavoriteIcon from '@material-ui/icons/Star';
import PrivateIcon from '@material-ui/icons/Archive';
import PublicIcon from '@material-ui/icons/LibraryBooks';
import AccountIcon from '@material-ui/icons/AccountCircleOutlined';
import AddIcon from '@material-ui/icons/NoteAdd';

import List from '@material-ui/core/List'; 
import './UserProfile.css';
import { ListItem, Button } from '@material-ui/core';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Export from '@material-ui/icons/CloudDownload';
import Import from '@material-ui/icons/CloudUpload';
import Collapse from '@material-ui/core/Collapse';
import TextField from '@material-ui/core/TextField'
import { parseBookmarkHtml, generateBookmarkHtml } from '../utils/bookmarkUtils';

import CollectionCard from '../components/CollectionCard';
import UserCollection from '../components/UserCollection'
import { COLLECTIONSTATE } from '../globalEnum';
import TextWrapper from './../components/TextWrapper'

var FileSaver = require('file-saver');
let addBookmarkSubscribe = null;
let deleteBookmarkSubscribe = null;


const styles = {
    avatar: {
        width: "260px",
        height: "260px"
    },
    icon: {
        fontSize: "20px"
    },
    list: {
        maxHeight: "70%", 
        width: "80%", 
        overflow: "auto",
        marginBottom: "10%",
    },
    listItem: {
        fontSize: "18px",
        fontFamily: "Quicksand",
        fontWeight: "100",
        marginLeft: "10%",
    },
    listItemIcon: {
        marginRight: "10%"
    },
    listItemText: {
        width: "50%",
    },
    listItemNum: {
        marginRight: "10%",
        marginLeft: "auto",
        color: "rgb(183, 183, 183)"
    },
    leftAlign: {
        marginRight: "auto",
        marginLeft: "50px"
    },
    button: {
        width: "80%",
        margin: "0 auto",
        fontWeight: "100",
        backgroundColor: "rgb(239, 239, 239)"
    },
    textField: {
        width: "80%",
        marginBottom: "10px"
    }
}

class MyProfile extends React.Component {
    constructor(props){
        super(props)
    }
    state = {
        userID: this.props.id ? this.props.id : this.props.location.id,
        currCollectionId: undefined,
        open: {
            'private': true,
            'published': false,
            'favorite': false
        },
        favoriteCollectionId: "",
    }
    importCollectionsFunc
    addCollectionFunc
    addCollectionRefetch


    componentDidMount(){
        if(this.refetch) this.refetch();
    }
    
    chooseCollection = id => {
        this.setState({
            currCollectionId: id,
        })
    }

    handleOpen = type => {
        const prevOpen = this.state.open;
        prevOpen[type] = !prevOpen[type];
        this.setState({
            open: prevOpen,
        })
    }

    onImport = event => {
        const file = event.target.files[0];
        var reader = new FileReader();
        reader.onload = async event => {
            var text = reader.result;
            const uploaded = parseBookmarkHtml(text);
            await this.importCollectionsFunc({
                variables: {
                    userID: this.state.userID,
                    collections: uploaded
                }
            })
            this.refetch();
        }
        reader.readAsText(file);
    }

    onExport = collections => {
        const text = generateBookmarkHtml(collections);
        var blob = new Blob([text], {type: "text/plain;charset=utf-8"});
        FileSaver.saveAs(blob, `bookmark.html`);
    }

    hanleAddCollection = event => {
        console.log(event.target)
        this.setState({
            favoriteCollectionId: event.target.value
        })
    }
    onAddCollection = (collection) => {
        console.log("Add collection")
        console.log("userID",this.state.userID)
        console.log("collection", collection)
        this.addCollectionFunc({
            variables: {
                userID: this.state.userID,
            }
        })
        this.refetch();
    }


   onDeleteBookmark = () => {
       console.log("delete bookmark");

       this.refetch();
   }

   onAddBookmark = () => {
       this.refetch();
   }

   onClickStore = () => {
       this.refetch();
   }


   onSaveUpdate = () => {
       console.log("updated!");
       this.refetch();
   }
   
    render(){
        //const privateCollection = this.state.collections.filter(collection => collection.state === COLLECTIONSTATE.PRIVATE);
        //const publishedCollection = this.state.collections.filter(collection => collection.state === COLLECTIONSTATE.PUBLISHED)
        //const collection = this.state.collections.find(collection => collection.id == this.state.currCollection);
        //if(this.refetch) this.refetch();
        console.log("in myprogile", this.props.id);
        const userID = (this.props.id) ? this.props.id : this.props.location.id;
        return(
            <Query query={MY_PROFILE_QUERY} variables={{userID: userID}} >
            { ({ loading, error, data, subscribeToMore, refetch}) => {
                console.log("data", data);
                this.refetch = refetch
                if (loading) return <p>Loading...</p>
                if (error) return <p>Error :(((</p>
                var { name, avatar, collections } = data.user
                console.log(collections)

                const privateCollection   = collections.filter(collection => collection.state === COLLECTIONSTATE.PRIVATE)
                const publishedCollection = collections.filter(collection => collection.state === COLLECTIONSTATE.PUBLISHED)
                const favoriteCollection  = collections.filter(collection => collection.state === COLLECTIONSTATE.FAVORITE)
                const collection = collections.find(collection => collection.id === this.state.currCollectionId);

                let renderElem;
                if(collection){
                    if(collection){
                        if(collection.state === COLLECTIONSTATE.FAVORITE)
                            renderElem = <UserCollection onClickStore={this.onClickStore} whoAmI='Me' collection={collection}/>

                        else renderElem = <CollectionCard 
                            collection={collection} 
                            onDeleteBookmark={this.onDeleteBookmark}
                            onAddBookmark={this.onAddBookmark}
                            onSaveUpdate={this.onSaveUpdate}
                        />
                    }
                }
                else renderElem = <div />
                
                return(
                    <div className="UserProfile animated fadeIn slow">
                        <div className="UserProfile-flex-container">
                            <div className="UserProfile-margin-block" ></div>
                            <div className="UserProfile-left" >
                                <div className="inner-container">
                                    {
                                        avatar?
                                        <Avatar src={avatar} style={styles.avatar}/>:
                                        <Avatar style={styles.avatar}><AccountIcon style={styles.avatar}/></Avatar>
                                    }
                                    <div className="follows-container">
                                        <div className="top-block">{name}</div>
                                        <div className="block">
                                            <div><ListIcon style={styles.icon} /></div>
                                            <div style={styles.leftAlign}>{collections.length} collections</div>
                                        </div>
                                        <div className="block">
                                            <div><ListIcon style={styles.icon} /></div>
                                            <div style={styles.leftAlign}>{privateCollection.length} private</div>
                                        </div>
                                        <div className="block">
                                            <div><ListIcon style={styles.icon} /></div>
                                            <div style={styles.leftAlign}>{publishedCollection.length} published</div>
                                        </div>
                                        <div className="block">
                                            <div><ListIcon style={styles.icon} /></div>
                                            <div style={styles.leftAlign}>{favoriteCollection.length} favorite</div>
                                        </div>
                                    </div>
                                </div>
                                <div className="public-card-container">
                                    { renderElem }
                                </div>
                            </div>
                            <div className="UserProfile-right" >
                                <div className="top-block">Collections</div>
                                <List style={styles.list}>
                                    <ListItem button onClick={() => this.handleOpen('private')}>
                                        <PrivateIcon style={styles.listItemIcon}/> Private
                                        {this.state.open['private'] ? <ExpandLess /> : <ExpandMore />}
                                    </ListItem>
                                    <Collapse in={this.state.open['private']} timeout="auto" unmountOnExit>
                                        <List>
                                            {
                                                privateCollection.map(
                                                    (item, idx )=> 
                                                    <ListItem 
                                                        button 
                                                        key={idx}
                                                        style={styles.listItem}
                                                        onClick={() => this.chooseCollection(item.id)}
                                                    >
                                                        <div style={styles.listItemText}>{item.title}</div>
                                                        <div style={styles.listItemNum}>{item.bookmarks.length}</div>
                                                    </ListItem>
                                                )
                                            }
                                        </List>
                                    </Collapse>
                                    <ListItem button onClick={() => this.handleOpen('published')}>
                                        <PublicIcon style={styles.listItemIcon}/> Published
                                        {this.state.open['published'] ? <ExpandLess /> : <ExpandMore />}
                                    </ListItem>
                                    <Collapse in={this.state.open['published']} timeout="auto" unmountOnExit>
                                        <List>
                                            {
                                                publishedCollection.map(
                                                    (item, idx )=> 
                                                    <ListItem 
                                                        button 
                                                        key={idx}
                                                        style={styles.listItem}
                                                        onClick={() => this.chooseCollection(item.id)}
                                                    >
                                                        <div style={styles.listItemText}>{item.title}</div>
                                                        <div style={styles.listItemNum}>{item.bookmarks.length}</div>
                                                    </ListItem>
                                                )
                                            }
                                        </List>
                                    </Collapse>
                                    <ListItem button onClick={() => this.handleOpen('favorite')}>
                                        <FavoriteIcon style={styles.listItemIcon}/>Favorite
                                        {this.state.open['favorite'] ? <ExpandLess /> : <ExpandMore />}
                                    </ListItem>
                                    <Collapse in={this.state.open['favorite']} timeout="auto" unmountOnExit>
                                        <List>
                                            {
                                                favoriteCollection.map(
                                                    (item, idx )=> 
                                                    <ListItem 
                                                        button 
                                                        key={idx}
                                                        style={styles.listItem}
                                                        onClick={() => this.chooseCollection(item.id)}
                                                    >
                                                        <div style={styles.listItemText}>{item.title}</div>
                                                        <div style={styles.listItemNum}>{item.bookmarks.length}</div>
                                                    </ListItem>
                                                )
                                            }
                                        </List>
                                    </Collapse>
                                </List>
                                <Mutation mutation={IMPORT_COLLECTIONS_MUTATION}>
                                {   importCollections => {
                                        this.importCollectionsFunc = importCollections
                                        return(
                                            <div style={{width:"100%", marginBottom: "10px"}}>
                                                <input
                                                    type="file"
                                                    accept=".html"
                                                    style={{ display: 'none' }}
                                                    id="import-bookmarks"
                                                    onChange={this.onImport}
                                                />
                                                <label htmlFor="import-bookmarks">
                                                    <Button style={styles.button}>
                                                        Import Bookmarks<Import style={{marginLeft: "10%"}}/>
                                                    </Button>
                                                </label>
                                            </div>
                                        )
                                    }
                                }
                                </Mutation>
                                <div style={{width:"100%", marginBottom: "10px"}}>
                                    <Button onClick={() => this.onExport(collections)} style={styles.button}>
                                        Export Bookmarks<Export style={{marginLeft: "10%"}}/>
                                    </Button>
                                </div>
                                <Mutation mutation={ADD_COLLECTION_MUTATION}>
                                {   addCollection => {
                                        this.addCollectionFunc = addCollection
                                        return(
                                            <>
                                                <div style={{width:"100%", marginBottom: "10px"}}>
                                                    <Button onClick={event => this.onAddCollection()} style={styles.button}>
                                                        Create New Collection<AddIcon style={{marginLeft: "10%"}}/>
                                                    </Button>
                                                </div>
                                            </>
                                        )
                                    }  
                                }
                                </Mutation>
                            </div> 
                        </div>
                    </div>
                    )
                }  
            }
            </Query>
        )
    }
}

export default MyProfile;