import React from 'react';
import Login from '../containers/login/Login';
import Register from '../containers/login/Register';
import { Route, Switch, NavLink } from 'react-router-dom';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar'
import './LoginRegister.css';

const styles = {
    button: {
        width: "50%",
        fontFamily: "Quicksand",
        fontWeight: "100",
        fontSize: "15px",
    },
    link: {
        textDecoration: "none",
        color: "black"
    },
    avatar: {
        // width: '100px',
        // height: '100px'
        width: '10em',
        height: '10em'
    }
}
class LoginRegister extends React.Component {
    render(){
        const icon_title = <div className="icon-title-container"> 
                                <div className="icon">
                                    <Avatar style={styles.avatar} src="/images/icon.jpg" />
                                </div>
                                <div className="title"> Ethereal </div>
                                <div className="subtitle">Manage your bookmarks with grace.</div>
                            </div>
        return(
            <body className="body-container">
                 <div className="LoginRegister animated fadeIn">
                    <div className="left-container">
                        <div className="navbar">
                            <div className="button">
                                <Button style={styles.button}>
                                    <NavLink to="/login" style={styles.link}>Login</NavLink>
                                </Button>
                            </div>
                            <div className="button">
                                <Button style={styles.button}>
                                    <NavLink to="/register" style={styles.link}>Register</NavLink>
                                </Button>
                            </div>
                        </div>
                        <Route  exact path="/"
                                render={props => icon_title}/>
                        <Route  exact path="/login"
                                render={props => icon_title}/>
                        {/* <div className="block"></div> */}
                        <div className="form">
                            <Switch>
                                <Route 
                                    exact path="/"
                                    render={props => <Login {...props} onLogin={this.props.onLogin} />}
                                />
                                <Route 
                                    exact path="/login"
                                    render={props => <Login {...props} onLogin={this.props.onLogin} />}
                                />
                                <Route 
                                    exact path="/register"
                                    render={props => <Register {...props} />}
                                />
                            </Switch>
                        </div>
                    </div>
                    <div className="right-container">
                        <img src="/images/web_final_login.jpg" />
                    </div>
                </div>
            </body>
        )
    }
}

export default LoginRegister;