import { gql } from 'apollo-boost'

const DELETE_BOOKMARK_SUBSCRIPTION = gql`
    subscription{
        deleteBookmarkSub{
            data{
                collectionId
                id
                title
                description
                url
                icon
                shortUrl
            }
        }
    }
`
const IMPORT_COLLECTION_SUBSCRIPTION = gql`
    subscription{
        importCollectionSub{
            bookmarks{
                collectionId
                id
                title
                description
                url
                icon
                shortUrl
            }
            collections{
                id
                title
                description
                tags
                state
                publishedTime
            }
        }
    }
`
const ADD_BOOKMARK_SUBSCRIPTION = gql`
    subscription{
        addBookmarkSub {
            data{
                collectionId
                id
                title
                description
                url
                icon
                shortUrl
            }
        }
    }
`

export { ADD_BOOKMARK_SUBSCRIPTION, IMPORT_COLLECTION_SUBSCRIPTION, DELETE_BOOKMARK_SUBSCRIPTION};