import React from 'react';
import App from './App';
import LoginRegister from './pages/LoginRegister';
import { Route, Redirect } from 'react-router-dom';


class OuterApp extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            loggedIn: false,
            userID: null,
        }
    }

    onLogout = () => {
        console.log("you logged out!");
        this.setState({
            loggedIn: false,
        })
        sessionStorage.setItem('login', 'false');
        console.log("After logout: ", sessionStorage.getItem('login'));
    }
    
    onLogin = id => {
        console.log("you logged in!");
        this.setState({
            loggedIn: true,
            userID: id,
        })
        sessionStorage.setItem('login', 'true');
        sessionStorage.setItem('userID', id);
        console.log("After login: ", sessionStorage.getItem('login'));
    }

    render(){
        const login = sessionStorage.getItem('login');
        const userID = sessionStorage.getItem('userID');
        console.log(login);
        return(
            (login==="true")? <App userID={userID} onLogout={this.onLogout}/>:<LoginRegister onLogin={this.onLogin}/>
        )
    }
}

export default OuterApp;