import React from 'react';
import TextWrapper from '../../components/TextWrapper';
import Button from '@material-ui/core/Button';
import "./Login.css";
import { NavLink } from 'react-router-dom';
import { Mutation } from 'react-apollo';
import { LOGIN_MUTATION } from '../../graphql/mutations';
const validator = require('validator');

const styles = {
    button: {
        width: "100%",
        fontFamily: "Quicksand",
        fontWeight: "100",
        fontSize: "15px",
    },
    link: {
        textDecoration: "none",
        color: "black"
      },
}

class Login extends React.Component {
    state = {
        email: "",
        password: "",
        emptyEmail: false,
        emptyPassword: false,
        invalidEmail: false,
        loginStatus: "LOGGED_OUT"
    }
    loginCheckFunc

    onEmailInput = e => {
        this.setState({
            email: e.target.value.trim(),
        })
        if(e.target.value.length > 0){
            this.setState({
                emptyEmail: false
            })
        }
    }

    onPasswordInput = e => {
        this.setState({
            password: e.target.value,
        })
        if(e.target.value.length > 0){
            this.setState({
                emptyPassword: false
            })
        }
    }

    handleLogin = () => {
        //console.log("handling login!");
        if(this.validateForm()){
            //console.log("validated login input!");
            this.loginCheckFunc({
                variables: {
                    email: this.state.email,
                    password: this.state.password
                }
            })
            //console.log("from server: ", data);
            //this.props.onLogin();
            return;
        }
    }

    validateForm = () => {
        //console.log("validating!");
        if(this.state.email.length === 0){
            this.setState({
                emptyEmail: true,
            })
        }
        else{
            if(!validator.isEmail(this.state.email)){
                this.setState({
                    invalidEmail: true,
                })
            }
            else{
                this.setState({
                    invalidEmail: false,
                })
            }
        }
        if(this.state.password.length === 0)
            this.setState({
                emptyPassword: true,
            })
        return (this.state.email.length > 0) && 
                (this.state.password.length > 0) &&
                    (validator.isEmail(this.state.email));
    }

    render(){
        return(
            <Mutation mutation={LOGIN_MUTATION}>
                {(loginCheck, {data}) => {
                    this.loginCheckFunc = loginCheck
                    let warningMessage = null;
                    if(data) {
                        if(data.loginCheck.status === "SUCCESS") {
                            this.props.onLogin(data.loginCheck.id)
                        }
                        else{
                            warningMessage = data.loginCheck.status;
                        }
                    }
                    return(
                        <div className="box">
                            <div className="input-container animated slideInDown">
                                <TextWrapper
                                    label="Email"
                                    value={this.state.email}
                                    handleChange={this.onEmailInput}
                                />
                            </div>
                            <div>
                                {
                                    (this.state.emptyEmail)?
                                        (<div className="warning">Email should not be empty!</div>):
                                        ((this.state.invalidEmail)?
                                            (<div className="warning">Invalid email format!</div>):
                                            ((warningMessage==="EMAIL_UNEXIST_ERROR")?
                                                (<div className="warning">Email does not exist!</div>):
                                                (<div></div>)
                                            )
                                        )
                                }
                            </div>
                            <div className="input-container animated slideInDown">
                                <TextWrapper 
                                    label="Password"
                                    value={this.state.password}
                                    handleChange={this.onPasswordInput}
                                />
                            </div>
                            <div>
                                {
                                    (this.state.emptyPassword) ? 
                                    (<div className="warning">Password should not be empty!</div>) : 
                                    ((warningMessage==="PASSWORD_UNMATCH_ERROR")?
                                        <div className="warning">Wrong password!</div>:
                                        <div></div>)
                                }
                            </div>
                            <div className="animated slideInUp">
                                <div className="button-container animated pulse infinite">
                                    <Button style={styles.button} onClick={this.handleLogin}>
                                        Login
                                    </Button>
                                </div>
                            </div>
                        </div>
                    )
                }}
            </Mutation>
            
        )
    }
}

export default Login;