import { gql } from 'apollo-boost'

const LOGIN_MUTATION = gql`
    mutation loginCheck(
        $email: String!
        $password: String!
    ){
        loginCheck(
            data: {
                email: $email
                password: $password
            }
        ){
            status
            id
        }
    }
`
const CREATE_USER_MUTATION = gql`
    mutation createUser(
        $id: String!
        $name: String!
        $email: String!
        $password: String!
        $avatar: String!
    ){
        createUser(
            data: {
                id: $id
                name: $name
                email: $email
                password: $password
                avatar: $avatar
            }
        ){
            status
        }
    }
`

const DELETE_BOOKMARK_MUTATION = gql`
    mutation deleteBookmark(
        $id: String!
    ){
        deleteBookmark(
            id: $id
        ){
            id
            title
            description
            url
            icon
            shortUrl
        }
    }
`
const UPDATE_COLLECTION_MUTATION = gql`
    mutation updateCollection(
        $collectionId: String!
        $title: String!
        $description: String!
        $state: Int!
        $publishedTime: String
    ){
        updateCollection(
            collectionId: $collectionId
            data: {
                title: $title
                description: $description
                state: $state
                publishedTime: $publishedTime
            }
        ){
            id
            title
        }
    }
`

const UPDATE_COLLECTION_STATE_MUTATION = gql`
    mutation updateCollection(
        $collectionId: String!
        $state: Int!
    ){
        updateCollection(
            collectionId: $collectionId
            data: {
                state: $state
            }
        ){
            id
            title
        }
    }
`

const ADD_BOOKMARK_MUTATION = gql`
    mutation addBookmark(
        $collectionId: String!
        $url: String!
        $title: String!
    ){
        addBookmark(
            collectionId: $collectionId
            data: {
                url: $url
                title: $title
            }
        ){
            id
            title
            description
            url
            icon
            shortUrl
        }
    }
`

const IMPORT_COLLECTIONS_MUTATION = gql`
    mutation importCollections(
        $userID: String!
        $collections: [CollectionInput!]
    ){
        importCollections(
            userID: $userID
            data: {
                collections: $collections
            }
        )
    }
`

const ADD_COLLECTION_MUTATION = gql`
    mutation addCollection(
        $userID: String!
        $collectionId: String
        $collections: favoriteCollectionInput
    ){
        addCollection(
            userID: $userID
            collectionId: $collectionId
            data: {
                collections: $collections
            }
        )
    }
`

const DELETE_COLLECTION_MUTATION = gql`
    mutation deleteCollection(
        $collectionId: String!
    ){
        deleteCollection(
            collectionId: $collectionId
        )
    }
`

export { ADD_BOOKMARK_MUTATION,
        DELETE_BOOKMARK_MUTATION,
        IMPORT_COLLECTIONS_MUTATION, 
        ADD_COLLECTION_MUTATION, 
        DELETE_COLLECTION_MUTATION,
        UPDATE_COLLECTION_MUTATION,
        UPDATE_COLLECTION_STATE_MUTATION,
        CREATE_USER_MUTATION,
        LOGIN_MUTATION
    };