import React from 'react';
import './App.css';

import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import IconButton from "@material-ui/core/IconButton";
import InputAdornment from "@material-ui/core/InputAdornment";
import SearchIcon from "@material-ui/icons/Search";

import TextField from '@material-ui/core/TextField';
import TextWrapper from './components/TextWrapper';
import Feeds from './containers/Feeds';
import UserProfile from './containers/UserProfile';
import MyProfile from './containers/MyProfile'
import UserSearchResults from './containers/UserSearchResults';

import { Switch, Route, NavLink } from 'react-router-dom';

const styles = {
  button: {
    backgroundColor: "rgb(239, 239, 239)",
    height: "54px",
    width: "15%",
    marginLeft: "1%",
    marginRight: "1%",
    fontFamily: "Quicksand"
  },
  link: {
    textDecoration: "none",
    color: "black"
  },
  buttonLink:{
    color: "rgb(173, 173, 173)"
  },
  avatar: {
    marginLeft: "2%",
    marginRight: "5%",
    width: "54px",
    height: "54px"
  },
  textField: {
    width: "100%",
  },
}


class App extends React.Component {

  state = {
    searchValue: ""
  }

  handleSearchChange = event => {
    this.setState({
      searchValue: event.target.value,
    })
  }

  clickSearch = () => {
    //alert("ehllo!");
  }

  render(){
    console.log(this.props.userID);
    //localStorage.setItem('login', "true");
    return (
      <div className="App">
        <div className="App-header">
          <Avatar src="images/icon.jpg" style={styles.avatar} />
          <Button style={styles.button}>
              <NavLink to="/feeds" style={styles.link}>Feeds</NavLink>
          </Button>
          <Button style={styles.button}>
              <NavLink to="/myprofile" style={styles.link}>My Profile</NavLink>
          </Button>
          <Button style={styles.button} onClick={this.props.onLogout}>
              <NavLink to="/" style={styles.link}>Logout</NavLink>
          </Button>
          <div className="App-input">
              <TextField
                label="Search..."
                variant="outlined"
                style={styles.textField}
                value={this.state.searchValue}
                onChange={this.handleSearchChange}
                InputProps={{
                    endAdornment: (
                      <InputAdornment>
                        <IconButton onClick={this.clickSearch}>
                          <NavLink style={styles.buttonLink} to="/search"><SearchIcon/></NavLink>
                        </IconButton>
                      </InputAdornment>
                    )
                }}
              />
          </div>
        </div>
        <Switch>
          <Route 
            exact path="/"
            render={props => 
              <Feeds 
                id={this.props.userID}
                {...props}
              />
            }
          />
          <Route 
            exact path="/feeds"
            render={props => 
              <Feeds 
                id={this.props.userID}
                {...props}
              />
            }
          />
          <Route 
            exact path="/myprofile"
            render={props => 
              <MyProfile 
                id={this.props.userID}
                {...props} 
              />}
          />
          <Route
            exact path="/user/:id"
            render={
              props=>
              <UserProfile
                currentUserId={this.props.userID}
                {...props}
            />}
          />
          <Route
            exact path="/search"
            render={
              props=>
                <UserSearchResults 
                  searchStr={this.state.searchValue} 
                  currentUserId={this.props.userID}
                  {...props}
                />
            }
          />
        </Switch>
      </div>
    );
  }
}

export default App;
