import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { yellow, blueGrey } from '@material-ui/core/colors';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Divder from '@material-ui/core/Divider';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Fab from '@material-ui/core/Fab'
import Tooltip from '@material-ui/core/Tooltip'
import AddIcon from '@material-ui/icons/Add';

import { Query, Mutation } from 'react-apollo';
import { ADD_BOOKMARK_MUTATION } from '../graphql/mutations';
import { validURL } from './../utils/urlValidateUtils'

const useStyles = makeStyles(theme => ({
    fab: {
        backgroundColor: "#C0A5FF",
        '&:hover': {
            backgroundColor: "#6B77E0",
        },
    },
    dialog: {
        color: "#4E5167"
    },
    button: {
        borderColor: "#4E5167",
        color: "#4E5167",
        borderColor: "#4E5167",
    },
}));

export default function Bookmark(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [title, setTitle] = React.useState("");
    const [url, setUrl] = React.useState("");
    const [err, setErr] = React.useState('')
    const { collectionTitle, collectionId} = props
    var addBookmarkFunc;

    function handleTitleChange(e){
        setTitle(e.target.value);
    }

    function handleUrlChange(e){
        setUrl(e.target.value.trim())
    }

    function handleClickOpen() {
        setOpen(true);
    }

    function handleClose() {
        setOpen(false)
        setUrl("")
    }

    const handleAddBookmark = async e => {
        e.preventDefault();

        if(validURL(url)){
            await addBookmarkFunc({
                variables: {
                    collectionId: collectionId,
                    url: url,
                    title: title.trim()
                }
            })
            setUrl("");
            setTitle("");
            setOpen(false)
            props.onAddBookmark();
        }
        else setErr('Url is invalid!!!')
    }

    return (
        <div>
            <Mutation mutation={ADD_BOOKMARK_MUTATION}>
                {addBookmark => {
                    addBookmarkFunc = addBookmark;
                    return(
                            <div>
                                <div className="animated pulse infinite">
                                    <Tooltip title='Add a new bookmark'>
                                        <Fab aria-label="Add" className={classes.fab} onClick={handleClickOpen}>
                                            <AddIcon />
                                        </Fab>
                                    </Tooltip>
                                </div>

                                <form onSubmit={handleAddBookmark} id='bookmark'>
                                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                                    <DialogTitle id="form-dialog-title" className={classes.dialog}>Create new bookmark</DialogTitle>
                                    <Divder variant='middle' />
                                    <DialogContent>
                                        {`Add this page to the collection : ${collectionTitle}`}
                                    <DialogContentText>
                                    </DialogContentText>
                                        <TextField
                                            autoFocus
                                            margin="dense"
                                            id="name"
                                            label="URL"
                                            type="url"
                                            fullWidth
                                            onChange={handleUrlChange}
                                            value={url}
                                            required
                                            inputProps={
                                                { required: 'required'}
                                            }
                                            helperText={err}
                                        />
                                        <TextField
                                            autoFocus
                                            margin="dense"
                                            id="name"
                                            label="Title"
                                            type="text"
                                            fullWidth
                                            onChange={handleTitleChange}
                                            value={title}
                                            helperText='Describe this bookmark'
                                        />
                                    
                                    </DialogContent>
                                    <DialogActions>
                                    <Button onClick={handleClose} className={classes.button}>
                                        Cancel
                                    </Button>
                                    <Button type='submit' form='bookmark' className={classes.button}>
                                        Create
                                    </Button>
                                    </DialogActions>
                                </Dialog>
                                </form>
                            </div>
                    )
                }}
            </Mutation>
        </div>
    );
}