const rp = require("request-promise");
const $ = require("cheerio");

function getUrlMetaData(url){
    return rp(url)
        .then(html => {
            const metas = $("meta", html);
            const links = $("link", html);

            const result = {
                description: "",
                favicon: "",
            }

            for(let i = 0; i < metas.length; ++i){
                if(metas[i].attribs.name){
                    if(metas[i].attribs.name.includes("description")){
                        result["description"] = metas[i].attribs.content;
                        break;
                    }
                }
            }
            //console.log(links);
            for(let i = 0; i < links.length; ++i){
                if(links[i].attribs.rel){
                    if(links[i].attribs.rel.includes("icon")){
                        result["favicon"] = links[i].attribs.href;
                        break;
                    }
                }
            }
            return result;
        })
        .catch(err => {console.log(err);});
}

