import React from 'react';
import UserCard from '../components/UserCard';
import { Query } from 'react-apollo';
import { SEARCH_USERS_QUERY, SEARCH_USERS_COLLECTIONS_QUERY } from '../graphql/queries';
import './UserSearchResults.css';

class UserSearchResults extends React.Component {
    render(){
        const { currentUserId, searchStr } = this.props
        return(
            <Query query={ SEARCH_USERS_QUERY } variables={{name: searchStr}}>
                { ({ loading, error, data, subscribeToMore, refetch}) => {
                    console.log(searchStr);
                    console.log("result", data);
                    return(
                        <div className="page animated fadeIn slow">
                            <div className="title-container">Search Results</div>
                            <div className="lower-container">
                                {
                                    data.users?
                                    data.users.map((user, idx) => <UserCard currentUserId={currentUserId} user={user} key={idx} />)
                                    :<div />   
                                }
                            </div>
                        </div>
                    )
                }}
            </Query>
        )
    }
}

export default UserSearchResults;

