import React from 'react';
import { Query, Mutation } from 'react-apollo'
import { FEEDS_QUERY } from './../graphql/queries'
import FeedCard from '../components/FeedCard';
import Divider from '@material-ui/core/Divider';
import { COLLECTIONSTATE } from '../globalEnum';
import UpIcon from '@material-ui/icons/KeyboardArrowUp';
import DownIcon from '@material-ui/icons/KeyboardArrowDown';
import './Feeds.css';


class Feeds extends React.Component {
    constructor(props){
      super(props)
      this.refetch = null
    }

    componentDidMount(){
      if(this.refetch) this.refetch();
    }

    render(){
      const { id } = this.props // Given userID render one's following feeds
    
      return(
        <Query query={FEEDS_QUERY} >
          { ({ loading, error, data, subscribeToMore, refetch}) => {
            if (loading) return <p>Loading...</p>
            if (error) return <p>Error :(((</p>
            this.refetch = refetch

            const feedCards = data.collections.map((collection, idx) => {
              return(
                <div key={idx} className="Feedcard-container"> 
                    <FeedCard userID={id} collection={collection} />
                </div>)
            })
            const columnsNum = 3
            let feedColumns = []
            let feeds = []
            for(let i=0 ; i < columnsNum ; ++i)
              feedColumns.push(Array(0))
            for(let i=0 ; i < feedCards.length ; ++i)
              feedColumns[i%columnsNum].push(feedCards[i])
            for(let i=0 ; i < feedColumns.length ; ++i)
              feeds.push(
                <div key={i} className="Feeds-row">
                  { feedColumns[i] }
                </div>
              )
            return (
              <div className="Feeds animated fadeIn slow">
                <div className="Feeds-title">Feeds</div>
                <Divider style={{marginBottom: "20px"}}/>
                <div className="Feeds-flex-container">
                    <div className="Feeds-margin-block"></div>
                      {feeds}
                    <div className="Feeds-margin-block"></div>
                </div>
                <Divider style={{marginBottom: "20px"}}/>
                </div>
            )
            }
          }
        </Query>
      )
    }
}

export default Feeds;