import React from 'react';
import { ThemeProvider } from '@material-ui/styles';
import { makeStyles, createMuiTheme } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Chip from '@material-ui/core/Chip';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';
import { blueGrey } from '@material-ui/core/colors';
import { Button, CardActionArea } from '@material-ui/core';
import Divider from '@material-ui/core/Divider';

import AccountIcon from '@material-ui/icons/AccountCircleOutlined';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorderOutlined';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import GotoIcon from '@material-ui/icons/LabelImportantOutlined';
import { Redirect } from 'react-router-dom';


import BookmarksList from './BookmarksList';

const theme = createMuiTheme({
    typography: {
        fontFamily: 'Quicksand',
        color: "#4E5167",
    },
})

const useStyles = makeStyles(theme => ({
    card: {
        maxWidth: '1',
    },
    media: {
        height: 0,
        paddingTop: '100%', // 16:9
        borderRadius: '1px'
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: "#4E5167",
    },
    // button: {
    //     margin: theme.spacing(1),
    //     color: "#4E5167",
    //     borderColor: "#4E5167",
    // },
    chip: {
        margin: theme.spacing(1),
        backgroundColor: blueGrey[100],
    },
}));

const BookmarksCollectionCard = (props) => {
    const classes = useStyles(theme);
    const [expanded, setExpanded] = React.useState(false);
    const [clickId, setClickId] = React.useState(null);

    const { id, author, title, description, bookmarks, tags, likes, publishedTime } = props.collection;
    // const {onClick} = props.function

    function handleExpandClick() {
        setExpanded(!expanded);
    }

    function onClickVisit(){
        setClickId(author.id);
        console.log("authorID", author.id);
    }
    
    if(clickId) {
        if(clickId === props.userID) return <Redirect to={{pathname:"/myprofile", id: clickId}}/>
        return <Redirect to={{pathname:`/user/${clickId}`, currentUserId: clickId}} />
    }

    return (
        <ThemeProvider theme={theme}>
            <Card className={classes.card}>
            <CardHeader
                avatar={
                    author.avatar ?
                    <Avatar aria-label="author" className={classes.avatar} src={author.avatar} />
                    :<Avatar className={classes.avatar}><AccountIcon className={classes.icon}/></Avatar>
                }
                title={title}
                subheader={`Published by ${author.name}. At ${publishedTime}.`}
            />
                <Divider variant="inset" />
            <CardContent>
                <Typography variant='body2' color='textSecondary'> Description </Typography>
                <Typography variant='body1' color='textPrimary'  > {description} </Typography>
            </CardContent>

            <CardActions disableSpacing>
                {/* <Tooltip title='Save to your favorite bookmarks'>
                    <Button variant='outlined' size='small' 
                    className={classes.button}
                    > Save </Button>                
                </Tooltip> */}

                <IconButton onClick={onClickVisit}>
                    <GotoIcon />
                </IconButton>
                <Typography> {"Visit user profile"} </Typography>
                <IconButton
                className={clsx(classes.expand, {
                    [classes.expandOpen]: expanded,
                })}
                onClick={handleExpandClick}
                aria-expanded={expanded}
                aria-label="Show more"
                >
                <Tooltip title='Expand to see bookmarks'>
                    <ExpandMoreIcon />
                </Tooltip>       
                </IconButton>
            </CardActions>
            <Divider variant='middle'/>
            <CardContent>
                <Typography variant='body2' color='textSecondary'>
                    Tags
                    {tags.map((tag, idx) => 
                        <Chip
                        id={idx}
                        size="small"
                        label={tag}
                        clickable
                        className={classes.chip}
                        />
                    )}
                </Typography>
            </CardContent>
            <Collapse in={expanded} timeout="auto" unmountOnExit>
                <CardContent> <BookmarksList bookmarks={bookmarks} /> </CardContent>
            </Collapse>
            </Card>
        </ThemeProvider>
        
    );
}
export default BookmarksCollectionCard;